import React, { Fragment, useState, useEffect } from "react";
import "../Styles/Mas.css";
import OptionsDrawellWithAnimation from "../components/animation/OptionsDrawellWithAnimation";
import Redes from "../components/buttons/redes";

const Mas = () => {

  const Estudios = [
    {
      nombre: "TECNÓLOGO EN AUTOMATIZACIÓN INDUSTRIAL", 
      fecha: "27/10/2023", 
      url:"https://firebasestorage.googleapis.com/v0/b/chat-a68fd.appspot.com/o/archivos%2F9536001965529CC1085306348A%20(1).pdf?alt=media&token=f5f8ffb6-a70f-4108-8e2a-6c54ffb161e7"
    },
    {
      nombre: "INTERPRETACION DE PLANOS MECANICOS",
      fecha: "06/10/2021", 
      url: "https://firebasestorage.googleapis.com/v0/b/chat-a68fd.appspot.com/o/archivos%2F9536002398595CC1085306348C.pdf?alt=media&token=2e824f41-b16d-4e56-ac66-7bc3cdf0e7f2"
    },
    {
      nombre: "PLANIFICACION DE PROYECTOS CON MICROSOFT PROJECT",
      fecha: "11/06/2020", 
      url: "https://firebasestorage.googleapis.com/v0/b/chat-a68fd.appspot.com/o/archivos%2F9536002086289CC1085306348C.pdf?alt=media&token=6841e398-4506-4607-b395-ea5b27116af6"
    },
    {
      nombre: "COREL DRAW X5 ",
      fecha: "03/06/2020", 
      url: "https://firebasestorage.googleapis.com/v0/b/chat-a68fd.appspot.com/o/archivos%2F9536002088795CC1085306348C.pdf?alt=media&token=4d0a387b-daa3-4090-aa98-3721cdd27b59"
    },
    {
      nombre: "INVENTOR",
      fecha: "03/06/2020", 
      url: "https://firebasestorage.googleapis.com/v0/b/chat-a68fd.appspot.com/o/archivos%2F9536002088805CC1085306348C.pdf?alt=media&token=387054a9-55fe-44c1-95e5-3965c5fa6489"
    },
    {
      nombre: "ELECTROTECNIA Y MEDIDAS",
      fecha: "08/09/2014", 
      url: "https://firebasestorage.googleapis.com/v0/b/chat-a68fd.appspot.com/o/archivos%2F920400804729CC1085306348C.pdf?alt=media&token=7fce341d-76f7-478b-a0dd-33f881f6d76b"
    },
    {
      nombre: "DISEÑO Y CONSTRUCCION DE TABLEROS DE DISTRIBUCION",
      fecha: "26/08/2014", 
      url: "https://firebasestorage.googleapis.com/v0/b/chat-a68fd.appspot.com/o/archivos%2F922300807065CC1085306348C.pdf?alt=media&token=9369266b-3119-466b-abb9-3d760363d351"
    },
    {
      nombre: "BACHILLER ACADEMICO",
      fecha: "29/11/2023", 
      url: "https://firebasestorage.googleapis.com/v0/b/chat-a68fd.appspot.com/o/archivos%2FCamScanner%2006-07-2022%2016.31%20(1).pdf?alt=media&token=77bc61fa-9356-4fc3-9289-30d2e0a90d23"
    }
  ]

  const urlHoja = "https://firebasestorage.googleapis.com/v0/b/chat-a68fd.appspot.com/o/archivos%2Fhoja_De_Vida_CRISTIAN_Actualizadado_2024.pdf?alt=media&token=1354246a-8fa1-4632-a9ef-20d4367a3603";

  const nota =String.raw`Esta página web no está diseñada con el objetivo principal de obtener un óptimo rendimiento, sino más bien para exhibir 
  las habilidades básicas necesarias en el desarrollo web. Aquí, cada sección, desde los proyectos hasta la experiencia laboral y el 
  perfil personal, sirve como muestra de mi dominio en tecnologías fundamentales como HTML, CSS, React y diversas librerías.
  
  Te encontrarás con una variedad de animaciones realizadas tanto con CSS como con librerías como Motion. Estas animaciones no buscan 
  necesariamente la eficiencia en el rendimiento, sino más bien, ilustrar mi capacidad para crear efectos visuales dinámicos y cautivadores.
  
  Es importante destacar que el rendimiento no es el objetivo principal de esta página, sino más bien la demostración de habilidades y la 
  presentación de mi trayectoria profesional en el desarrollo de software. 
  ¡Gracias por visitar y explorar mi hoja de vida en línea`;
  


  // Estado para controlar el desplazamiento de la página
  const [lastScrollPos, setLastScrollPos] = useState(0);
  const [navVisible, setNavVisible] = useState(true);

  // Función para manejar el desplazamiento a la sección de "Estudios"
  const scrollToEstudios = () => {
    document.getElementById("estudios").scrollIntoView({ behavior: "smooth" });
  };

  // Función para manejar el desplazamiento a la sección de "Contacto"
  const scrollToContacto = () => {
    document.getElementById("contacto").scrollIntoView({ behavior: "smooth" });
  };

  // Función para manejar el desplazamiento a la sección de "Contacto"
  const scrollToNota = () => {
    document.getElementById("nota").scrollIntoView({ behavior: "smooth" });
  };

  const handleClickWeb = (link) => {
    window.open(link, "_blank");
  };

  return (
    <Fragment>
        <OptionsDrawellWithAnimation/>
      <div className="containerMas">
        {/* Barra de navegación */}
        <nav className={`navbar ${navVisible ? "" : "hidden"}`}>
          <ul className="nav-list">
            <li className="nav-item">
              <button onClick={scrollToEstudios}>Estudios</button>
            </li>
            <li className="nav-item">
              <button onClick={scrollToNota}>Importante</button>
            </li>
            <li className="nav-item">
              <button onClick={scrollToContacto}>Contacto</button>
            </li>
          </ul>
        </nav>

        {/* Sección de Estudios */}
        <section id="estudios" className="section">
          <h2>Estudios</h2>
          <div className="containerEstudios">
            {Estudios.map((item, index) =>{
              return(
                <div key={index} className="subContainerEstu">
                  <div className="contNameEst">
                    <span className="txtNameEst">{item.nombre}</span>
                  </div>
                  <div className="contFechEst">
                    <span className="txtNameEst">{item.fecha}</span>
                  </div>
                  <div className="contVerMas">
                    <span className="txtNameEst"
                    onClick={() => handleClickWeb(item.url)}>{"Ver >>"}</span>
                  </div>
                </div>
              );
            })}
          </div>
        </section>

        {/* Sección de Estudios */}
        <section id="nota" className="section" translate="no">
          <h2>Importante</h2>
          <span className="txtImportante" translate="no">
        Esta página web no está diseñada con el objetivo principal de obtener un óptimo rendimiento, sino más bien para exhibir las habilidades básicas necesarias en el desarrollo web. Aquí, cada sección, desde los proyectos hasta la experiencia laboral y el perfil personal, sirve como muestra de mi dominio en tecnologías fundamentales como HTML, CSS, React y diversas librerías. <br /><br />
        Te encontrarás con una variedad de animaciones realizadas tanto con CSS como con librerías como Motion. Estas animaciones no buscan necesariamente la eficiencia en el rendimiento, sino más bien, ilustrar mi capacidad para crear efectos visuales dinámicos y cautivadores. <br /><br />
        Es importante destacar que el rendimiento no es el objetivo principal de esta página, sino más bien la demostración de habilidades y la presentación de mi trayectoria profesional en el desarrollo de software. <br />
        <br />
        ¡Gracias por visitar y explorar mi hoja de vida en línea!
      </span>
        </section>

        {/* Sección de Contacto */}
        <section id="contacto" className="section">
          <h2>Contacto</h2>
          <span className="txtImportante" translate="no">
          ¡Gracias por tomarte el tiempo de explorar mi hoja de vida en línea! Espero que hayas encontrado útil y revelador el vistazo a mi experiencia y habilidades en el desarrollo web.<br />
          Si mi perfil ha despertado tu interés o si deseas obtener más información, no dudes en ponerte en contacto conmigo. Estoy ansioso por analizar cómo mi experiencia y conocimientos pueden ser de beneficio para tu equipo o proyecto.<br />
          <br />
          Para contactarme, puedes encontrar mis datos de contacto a continuación:<br />
          </span>
          <div className="divRedes">
            <Redes/>
          </div>
          <div className="divDescargar">
            <span className="txtImportante3">Descarga la hoja de vida </span>
            <span className="txtImportante2"
            onClick={() => handleClickWeb(urlHoja)}> {"Aquí."}</span>
          </div>
        </section>
      </div>
    </Fragment>
  );
};

export default Mas;
