        "https://firebasestorage.googleapis.com/v0/b/chat-a68fd.appspot.com/o/carryt%2FNewJVojrdqKPhe5AvNgrPRWy5O1MuWkvbsBeZB7Mzqf8eOHHpY3psY-PlkcvAukH7L0%3Dw526-h296-rw.png?alt=media&token=7b456611-d8ca-4187-8c91-b7785eb72512",
